.inputContainer {
    position: relative;
    margin-left: 20px;
    flex-grow: 1;
}

input,
input:focus,
input:not(:focus) {
    border: 0;
    border-bottom: 2px solid #979797;
    font-family: "NouvelR-Regular", sans-serif;
    outline: 0;
    color: #464646;
    padding: 7px;
    background: transparent;
    transition: .3s;
    flex-shrink: 1;
    flex-grow: 1;
}

input:focus {
    border-color: #5f5e5e;
    transition: .7s;
    font-family: "NouvelR-Regular", sans-serif;
    color: #3a3a3a;
}

input::placeholder {
    color: #817f7f;
    font-family: "NouvelR-Light", sans-serif;
}

.rightBlock {
    font-family: "NouvelR-Light", sans-serif;
    margin-left: 15px;
    border: none;
    display: flex;
    flex-grow: 1;

}

.label {
    text-align: right;
    flex-grow: 1;
}

