@media screen and (min-aspect-ratio: 13/9) {
    /* горизонтальная*/

    .inputContainer {
        margin-top: 15px;
        display: flex;
        -direction: row;
        flex-wrap: wrap;
        font-size: 1.1rem;
        margin-bottom: 15px;
    }

    .input {
        /*border: 1px solid #000;*/
        /*display: flex;*/
        flex-basis: 350px;
        max-width: 350px;
        /* height: 40px; */
        /* padding: 5px; */
        border-radius: 0;
        /*color: #656666*/
        border: 1px solid #d9d9d6;
        outline: none;
        font-size: 1.1rem;

    }

    .inputError {
        flex-basis: 350px;
        max-width: 350px;
        /* height: 40px; */
        font-family: inherit;
        /* padding: 5px; */
        border-radius: 0;
        outline: none;
        font-size: 1.1rem;
    }

    .checkboxAndInput {
        width: 306px;
        flex-basis: 350px;
        /* height: 40px; */
        /* padding: 5px; */
        border-radius: 0;
        /*color: #656666*/
        border: 1px solid #d9d9d6;
        outline: none;
        font-size: 1.1rem;
    }

    .label {
        display: inline-block;
        /* justify-content: right; */
        /*font-weight: bold;*/
        flex-basis: 150px;
        max-width: 150px;
        vertical-align: bottom;
        align-self: center;
        /* margin-right: 10px; */
        word-wrap: break-word;
    }
}

