
@media screen and (min-aspect-ratio: 13/9) {  /* горизонтальная*/

    .languageSkills {
        display: flex;
        flex-direction: row;
        align-items: center;
    }

    .button{
        width: 2rem;
        height: 2rem;
        font-family: "Renault", sans-serif;
        padding: 5px;
        border-radius: 0;
        /*color: #656666*/
        border: 1px solid #c3c3c3;
        background: white;
        outline: none;
        font-size: 1.4rem;
        display: flex;
        justify-content: center;
        align-items: center;
    }

    .buttonsContainer{
        z-index: 2;
        display: flex;
        position: relative;
        flex-direction: row;
        margin-left: 15px;
    }
}

@media screen and (max-aspect-ratio: 13/9) {  /* вертикальная*/

    .languageSkills {
        display: flex;
        flex-direction: column;
        /*align-items: flex-start;*/
    }

    .button{
        width: 2rem;
        height: 2rem;
        font-family: "Renault", sans-serif;
        padding: 5px;
        border-radius: 0;
        /*color: #656666*/
        border: 1px solid #c3c3c3;
        background: white;
        outline: none;
        font-size: 1.4rem;
        display: flex;
        justify-self: flex-end;
        justify-content: center;
        align-items: center;
    }

    .buttonsContainer{
        z-index: 2;
        display: flex;
        position: relative;
        flex-direction: row;
        margin-left: 15px;
    }
}

