.selectContainer {
    margin-top: 20px;
}

.select {
    padding: 5px 6px;
}

@media screen and (min-aspect-ratio: 13/9) {
    /* горизонтальная*/

    .selectContainer {
        display: flex;
        -direction: row;
        flex-wrap: wrap;
        margin-left: 50px;
    }

    .select {
        /*width: 250px;*/
        /*height: 32px;*/
        /*position: relative;*/
        /*min-width: 90px;*/
        /*height: 32px;*/
        /*margin: 0;*/
        /*z-index: 1;*/
        /*padding: 4px 33px 4px 10px;*/
        /*font-size: 18px;*/
        /*font-weight: 300;*/
        /*border-radius: 3px;*/
        /*line-height: normal;*/
        /*border: 1px solid rgba(0,0,0,.5);*/
        display: inline-block;
        width: 350px;
        padding: 8px 24px 8px 8px;
        border: 1px solid #d9d9d6;
        border-radius: 0;
        margin: 0;
        background-color: #fff;
        color: #000;
        font-family: inherit;
        font-size: 1.1rem;
        cursor: pointer;
        background-image: none;
        -webkit-appearance: none;
        -moz-appearance: none;
        outline: none;
        font-family: inherit;
        -webkit-appearance: none;
        -moz-appearance: none;
        background: transparent;
        background-image: url("data:image/svg+xml;utf8,<svg fill='black' height='38' viewBox='0 0 24 24' width='28' xmlns='http://www.w3.org/2000/svg'><path d='M7 10l5 5 5-5z'/><path d='M0 0h24v24H0z' fill='none'/></svg>");
        background-repeat: no-repeat;
        background-position-x: 100%;
        background-position-y: 50%;
        padding-right: 2rem;
    }



    .label {
        display: flex;
        justify-content: right;
        /*font-weight: bold;*/
        flex-basis: 150px;
        margin-right: 10px;
        text-align: right;
        font-size: 1.1rem;
        align-self: center;
        font-family: inherit;
    }




}

@media screen and (max-aspect-ratio: 13/9) {
    /* вертикальная*/

    .selectContainer {
        display: flex;
        flex-direction: column;
        flex-wrap: wrap;
        margin-left: 10px;
    }

    .select {
        /*width: 250px;*/
        /*height: 32px;*/
        /*position: relative;*/
        /*min-width: 90px;*/
        /*height: 32px;*/
        /*margin: 0;*/
        /*z-index: 1;*/
        /*padding: 4px 33px 4px 10px;*/
        /*font-size: 18px;*/
        /*font-weight: 300;*/
        /*border-radius: 3px;*/
        /*line-height: normal;*/
        /*border: 1px solid rgba(0,0,0,.5);*/
        display: inline-block;
        width: 350px;
        height: 55px;
        padding: 8px 24px 8px 8px;
        border: 1px solid #d9d9d6;
        border-radius: 0;
        margin: 0;
        background-color: #fff;
        color: #000;
        font-family: inherit;
        font-size: 1.1rem;
        cursor: pointer;
        background-image: none;
        -webkit-appearance: none;
        -moz-appearance: none;
        outline: none;
        font-family: inherit;
        -webkit-appearance: none;
        -moz-appearance: none;
        background: transparent;
        background-image: url("data:image/svg+xml;utf8,<svg fill='black' height='38' viewBox='0 0 24 24' width='28' xmlns='http://www.w3.org/2000/svg'><path d='M7 10l5 5 5-5z'/><path d='M0 0h24v24H0z' fill='none'/></svg>");
        background-repeat: no-repeat;
        background-position-x: 100%;
        background-position-y: 50%;
        padding: 1rem;
        padding-right: 2rem;
    }



    .label {
        display: flex;
        justify-content: left;
        /* font-weight: bold; */
        margin-right: 10px;
        text-align: left;
        font-size: 1.1rem;
        /* align-self: center; */
        font-family: inherit;
    }




}