.loadPhoto {
    margin: 20px;
    width: 150px;
    /*height: 20px;*/
    font-family: inherit;
    padding: 5px;
    border-radius: 0;
    /* border: 1px solid #d9d9d6; */
    outline: none;
    font-size: 1.1rem;
    background-color: #efdf00;
    border-radius: 4px;
    border: none;
    transition: .4s;
}

.loadPhoto:hover {
    cursor: pointer;
    background-color: rgb(252, 229, 137);
}

.mainDataContainer {
    width: 100%;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
}

.mainData {
    width: 63%;
    margin-top: 5px;
}

.photoContainer {
    margin-top: 15px;
    width: 32%;
    padding: 15px;
    display: flex;
    flex-direction: column;
    /*border: black 1px solid;*/
    align-items: center;
    justify-content: left;
}

.fileInput {
    display: none;
}

.image {
    width: 80%;
    height: auto;
    border-radius: 50%;
    /* border: 7px solid rgba(255, 218, 68, 0.377); */
    outline: none;
}

@media screen and (min-aspect-ratio: 13/9) {
    /* горизонтальная*/

    .mainDataContainer {
        width: 100%;
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
    }

    .mainData {
        width: 63%;
        margin-top: 5px;
    }

    .photoContainer {
        width: 32%;
        padding: 15px;
        display: flex;
        flex-direction: column;
        /*border: black 1px solid;*/
        align-items: center;
        justify-content: left;
    }

    .fileInput {
        display: none;
    }


    .imageInputDescription {
        text-align: center;
        width: 200px
    }
}

@media screen and (max-aspect-ratio: 13/9) {
    /* вертикальная*/

    .mainDataContainer {
        width: 100%;
        display: flex;
        flex-direction: column;
        flex-wrap: wrap;
    }

    .mainData {
        width: 70%;
    }

    .photoContainer {
        display: flex;
        flex-direction: column;
        /*border: black 1px solid;*/
        align-items: center;
        justify-content: left;
    }

    .fileInput {
        display: none;
    }

    .image {
        width: 80%;
        height: auto;

    }

    .loadPhoto {
        margin: 15px;
        width: 150px;
        /*height: 20px;*/
        font-family: inherit;
        padding: 5px;
        border-radius: 0;
        border: none;
        outline: none;
        font-size: 1.1rem;
    }

    .imageInputDescription {
        text-align: center;
        width: 200px
    }
}