


@media screen and (min-aspect-ratio: 13/9) {  /* горизонтальная*/
    .inputAndSelect{
        display: flex;
        flex-direction: row;
    }

    .buttonsContainer{
        z-index: 2;
        display: flex;
        position: relative;
        left: 90%;
    }

    .button{
        width: 2rem;
        font-family: "Renault", sans-serif;
        padding: 5px;
        height: 2rem;
        border-radius: 0;
        /*color: #656666*/
        border: 1px solid #c3c3c3;
        background: white;
        outline: none;
        font-size: 1.4rem;
        display: flex;
        justify-content: center;
        align-items: center;
    }
}

@media screen and (max-aspect-ratio: 13/9) {  /* вертикальная*/
    .inputAndSelect{
        display: flex;
        flex-direction: column;
    }

    .buttonsContainer{
        z-index: 2;
        display: flex;
        position: relative;
        left: 90%;
    }

    .button{
        width: 2rem;
        font-family: "Renault", sans-serif;
        padding: 5px;
        height: 2rem;
        border-radius: 0;
        /*color: #656666*/
        border: 1px solid #c3c3c3;
        background: white;
        outline: none;
        font-size: 1.4rem;
        display: flex;
        justify-content: center;
        align-items: center;
    }
}
