@media screen and (min-aspect-ratio: 13/9) {
    /* горизонтальная*/

    .container {
        margin-top: 15px;
        display: flex;
        -direction: row;
        flex-wrap: wrap;
        margin-left: 50px;
        font-size: 1.1rem;
        margin-bottom: 15px;
        font-family: inherit;

    }

    .description {
        font-size: 1.1rem;
    }

    .containerWithoutLabel {
        margin-top: 15px;
        display: flex;
        -direction: row;
        flex-wrap: wrap;
        font-size: 1.1rem;
        margin-bottom: 15px;
        font-family: inherit;
        padding-left: 10px;
        flex-grow: 1;
    }

    .checkbox {
        position: absolute;
        z-index: -1;
        opacity: 0;
    }

    .checkbox+label {
        font-size: 1.1rem;
        display: inline-flex;
        align-items: center;
        user-select: none
    }

    .checkbox+label::before {
        content: '';
        display: inline-block;
        width: 1.3em;
        height: 1.3em;
        flex-shrink: 0;
        flex-grow: 0;
        border: 1px solid #adb5bd;

        margin-right: 0.5em;
        background-repeat: no-repeat;
        background-position: center center;
        background-size: 50% 50%;
    }

    .checkbox:checked+label::before {
        border-color: #fc3;
        background-color: transparent;
        background-image: url(resources/checkMark.svg);
        /*background-image: url("data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 8 8'%3e%3cpath fill='%23fff' d='M6.564.75l-3.59 3.612-1.538-1.55L0 4.26 2.974 7.25 8 2.193z'/%3e%3c/svg%3e");*/
    }

    .label {
        display: inline-block;
        justify-content: right;
        flex-basis: 150px;
        width: 150px;
        text-align: right;
        vertical-align: bottom;
        align-self: center;
        margin-right: 10px;
        font-size: 1.1rem;
        word-wrap: break-word;
    }
}

