

@media screen and (min-aspect-ratio: 13/9) {  /* горизонтальная*/
    .container{
        display: flex;
        flex-direction: row;
        justify-content: center;
        align-items: center;
        margin: 0;
        padding: 0;
        margin-left: 15px;
        height: 15px;
        cursor: pointer;
    }


    .circle{
        font-size: 1.5rem;
        font-weight: 300;
        margin: 0;
        padding: 0;
    }

}

@media screen and (max-aspect-ratio: 13/9) {  /* вертикальная*/
    .container{
        display: flex;
        flex-direction: row;
        align-self: flex-start;
        align-items: center;
        margin: 0;
        padding: 0;
        margin-left: 25px;
        height: 15px;
        cursor: pointer;
    }


    .circle{
        font-size: 1.5rem;
        font-weight: 300;
        margin: 0;
        padding: 0;
    }

}