textarea::placeholder {
    font-family: "NouvelR-Light", sans-serif;
    color: #817f7f;
}

.label {
    margin-left: 15px;
    margin-right: 15px;
}

.container {
    margin-top: 20px;
}


@media screen and (min-aspect-ratio: 13/9) {
    /* горизонтальная*/

    .container {
        display: flex;
        /*-direction: row;*/
        /*flex-wrap: wrap;*/
        /*margin-top: 15px;*/
        /*margin-left: 50px;*/

        outline: none;
        font-size: 1.1rem;
    }

    .label {
        display: flex;
        justify-content: right;
        /*font-weight: bold;*/
        flex-basis: 150px;
        width: 150px;
        text-align: right;
        font-family: inherit;
    }

    .textAreaWrapper {

        width: 500px;
        max-width: 100%;
    }

    .input {
        display: flex;
        width: 100%;
        box-sizing: border-box;
        height: 120px;
        padding: 16px 8px;
        font-family: inherit;
        resize: vertical;
        /*transition: .8s;*/
        outline: none;
        font-size: 1.1rem;
        min-height: 100px;
        max-height: 520px;
    }

    .inputError {
        display: flex;
        width: 100%;
        box-sizing: border-box;
        height: 120px;
        padding: 16px 8px;
        font-family: inherit;
        resize: vertical;
        /*transition: .8s;*/
        outline: none;
        font-size: 1.1rem;
        min-height: 100px;
        max-height: 520px;

    }

    .lengthStatus {
        font-size: 12px;
        text-align: right;
        margin-top: 5px;
        margin-right: 5px;
    }

    .container {
        margin-bottom: 10px;
    }

}

@media screen and (max-aspect-ratio: 13/9) {
    /* вертикальная*/

    .container {
        display: flex;
        /*-direction: row;*/
        /*flex-wrap: wrap;*/
        /*margin-top: 15px;*/
        /*margin-left: 50px;*/
        flex-direction: column;
        outline: none;
        font-size: 1.2rem;
    }

    .label {
        display: flex;
        justify-content: right;
        /*font-weight: bold;*/

        width: 150px;
        text-align: left;
        font-family: inherit;
    }

    .textAreaWrapper {

        width: 500px;
        max-width: 100%;
    }

    .input {
        display: flex;
        width: 100%;
        box-sizing: border-box;
        height: 120px;
        padding: 16px 8px;
        font-family: inherit;
        resize: vertical;
        /*transition: .8s;*/
        outline: none;
        font-size: 1.1rem;
        min-height: 100px;
        max-height: 520px;
    }

    .inputError {
        display: flex;
        width: 100%;
        box-sizing: border-box;
        height: 120px;
        padding: 16px 8px;
        font-family: inherit;
        resize: vertical;
        /*transition: .8s;*/
        outline: none;
        font-size: 1.1rem;
        min-height: 100px;
        max-height: 520px;

    }

    .lengthStatus {
        font-size: 12px;
        text-align: right;
        margin-top: 5px;
        margin-right: 5px;
    }

    .container {
        margin-bottom: 15px;
        margin-top: 10px;
    }

}